/* ==========================================================================
   Product List Simple shortcode responsive styles - begin
   ========================================================================== */

@include ipad-landscape {

	.mkdf-pls-holder {

		$simple_list_columns: ('four', 'five', 'six');

			@for $i from 0 to length($simple_list_columns) {
				&.mkdf-#{nth($simple_list_columns,$i+1)}-columns {
					$column_width: 100%/3;

					.mkdf-pls-item {
						width: $column_width;

						@media only screen and (min-width: $ipad-portrait-plus-pixel) {
							&:nth-child(3n+1) {
								clear: both;
							}
						}
					}
				}
			}
	}
}

@include ipad-portrait {

	.mkdf-pls-holder {

		$simple_list_columns: ('two', 'three', 'four', 'five', 'six');

			@for $i from 0 to length($simple_list_columns) {
				&.mkdf-#{nth($simple_list_columns,$i+1)}-columns {
					$column_width: 50%;

					.mkdf-pls-item {
						width: $column_width;

						@media only screen and (min-width: $phone-landscape-plus-pixel) {
							&:nth-child(2n+1) {
								clear: both;
							}
						}
					}
				}
			}
	}
}

@include phone-landscape {

	.mkdf-pls-holder {

		$simple_list_columns: ('two', 'three', 'four', 'five', 'six');

			@for $i from 0 to length($simple_list_columns) {
				&.mkdf-#{nth($simple_list_columns,$i+1)}-columns {

					.mkdf-pls-item {
						width: 100%;
					}
				}
			}
	}
}
/* ==========================================================================
   Product List Simple shortcode responsive styles - end
   ========================================================================== */