/* ==========================================================================
   YITH Quick View Responsive styles - begin
   ========================================================================== */

@include ipad-landscape {

    #yith-quick-view-modal {

        #yith-quick-view-content {

            .summary {
                padding: 40px 20px 0;

                .mkdf-woo-social-share-holder  {
                    padding-top: 19px;
                    margin-top: 0;
                }
                .yith-wcwl-add-to-wishlist {
                    padding-top: 24px;
                    margin-top: 0;

                }
                .yith-wcwl-add-to-wishlist {
                    .yith-wcwl-add-button,
                    .yith-wcwl-wishlistexistsbrowse {
                        a {
                            line-height: 22px;
                            &:after {
                                line-height: 19px;

                            }
                        }

                    }
                    
                }
            }
        }
    }
}


@include ipad-landscape {
    body{
        position: relative; //in order to make popup scrollable
    }

    #yith-quick-view-modal {
        position: absolute;
        right: auto;
        bottom: auto;
        width: 100%;
        height: 100%;

        .yith-wcqv-wrapper{
            position: absolute;
        }

        #yith-quick-view-content {

            .images,
            .summary {
                    width: 100%;
            }

            .summary {
                padding: 28px 40px 22px;
                min-height: 400px;

                table.group_table {
                    td {
                        &.woocommerce-grouped-product-list-item__quantity {
                            width: 37%;
                        }
                    }
                }
            }

        }
         table.group_table {
             td {
                    box-sizing: border-box;
                    padding: 0 10px;
                    text-align: left;
                    display: inline-block;
                    
                }
         }

    }
}

@include phone-landscape {

    #yith-quick-view-modal {

        #yith-quick-view-content {

            .images,
            .summary {
                width: 100%;
            }

            .summary {
                padding: 32px 40px 28px;

                

                .variations {
                    tbody {
                        width: 100%;
                        display: block;

                        tr {
                            width: 100%;
                            padding-right: 0;
                        }
                    }
                }

                .mkd-quantity-buttons {
                    width: 100%;
                    text-align: center;
                }

                table.group_table {

                    .mkd-quantity-buttons {
                        width: 100%;
                        text-align: center;
                    }
                }

                div[itemprop="offers"] {
                    margin: 0 0 20px;
                }

                div[itemprop="description"] {
                    display: none;
                }

                form.cart {
                    margin: 0 0 20px;
                }
                .single_add_to_cart_button {
                    margin: 20px 0 0;
                }
            }

        }

    }
}
@include phone-portrait {
    #yith-quick-view-modal {
        #yith-quick-view-content {
            .summary  {
                .yith-wcwl-add-to-wishlist {
                    .yith-wcwl-add-button {
                        a {
                            border-right: 1px solid transparent;
                        }
                    }
                }
                .mkdf-woo-social-share-holder {
                    margin-top: 0px;
                }
            }
        }
    }
}
#yith-quick-view-modal {
    .yith-wcqv-wrapper {

        @include ipad-landscape {
            width: 95% !important;
        }
        
        @include ipad-portrait {
            width: 92% !important;
        }
        @include phone-landscape {
            min-height: 450px;
        }
        @include phone-portrait {
            width: 84% !important;
        }

    }
    
}

/* ==========================================================================
   YITH Quick View responsive styles - end
   ========================================================================== */