/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */

@include ipad-portrait {
	
	.mkdf-woocommerce-page {
		
		table.cart {

			.mkdf-quantity-buttons {
			    border: none;
			    margin: 0;
			    padding: 0;
			    width: 104px;
			    background-color: transparent;
			}
			tr {
				td.product-quantity {
					.mkdf-quantity-buttons {
						.mkdf-quantity-label {
							display: none;
						}
					}
				}
			}

			
			.mkdf-quantity-buttons {
				padding: 0 10px;
			}
			
			td.actions {
				
				.coupon {
					
					input[type="text"] {
						margin: 0 17px 21px 0;
						
					}
				}
			}
	
		}
	}
}
@include ipad-landscape {
	
	.mkdf-woocommerce-page {
		
		table.cart {

			tr.cart_item {
				td {
				    box-sizing: border-box;
				}
			}

			
		}
	}
}


@include phone-landscape {
	
	.mkdf-woocommerce-page {
		
		table.cart {
			
			tr {
				
				th, td {
					font-size: 12px;
				}
				
				&.cart_item {
					
					td {
						
						&.product-thumbnail {
							width: auto;
							padding-right: 0;
							
							img {
								display: none;
							}
						}
					}
				}
			}
			
			td.actions {
				
				.coupon {
					margin: 0 0 20px;
					
				}
				
				.coupon,
				> input,
				> button {
					float: none;
					display: block;
				}
			}
		}
	}
}
@include phone-portrait {
	.mkdf-woocommerce-page {

		.cart-collaterals {
			table {
				th {
					padding-right: 20px;

				}
			}
		}

		table.cart {
				
			td.actions {
				
				
				.coupon,
				input,
				button {
					float: none;
					
				}
			}
		}
	}


}
/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */