@media only screen and (max-width: $ipad-portrait) {

    .woocommerce-wishlist {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 17px 25px;
        }
    }
}

@include phone-landscape  {

    .woocommerce-wishlist {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 14px 20px;
            line-height: 2em;

            a {
                float: none;
                margin: 0 70% 15px 0;
            }
        }
        table.wishlist_table {
            tbody {
                tr {
                    td {
                        text-align: left;
                    }
                    td.product-name {
                        padding-left: 0px;
                        a {
                            font-size: 12px;
                        }
                    }
                    td.product-add-to-cart {
                        a {
                            height: 40px;
                            line-height: 41px;
                            padding: 0 10px;
                            font-size: 14px;
                        }
                    }
                    
                }
            }

        }
    }
}

@include phone-portrait {

    .woocommerce-wishlist {

        table.wishlist_table {

            tbody {

                tr {

                    td.product-thumbnail {

                        display: none;
                    }
                    
                }
            }
        }
    }
    .woocommerce {
        table.wishlist_table {
            tr {
                td.product-remove {
                    padding-right: 8px;
                }
            }
        }
    }
    
}

