/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - begin
   ========================================================================== */

@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape) {
    
    .mkdf-woo-single-page {
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 12px 7px;
                    }
                }
            }
        }
    }
}

.mkdf-woo-single-page.mkdf-woo-single-thumb-on-left-side {
	.mkdf-single-product-content {
		.images {
			.woocommerce-product-gallery__image {
				&:first-child {
					@media only screen and (min-width: $laptop-landscape-plus-pixel) and (max-width: $laptop-landscape-large) {
						left: 110px;
						width: calc(100% - 110px);
					}

					@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape) {
						left: 102px;
						width: calc(100% - 102px);
					}

					@media only screen and (min-width: $ipad-portrait-plus-pixel) and (max-width: $ipad-landscape) {
						left: 160px;
						width: calc(100% - 160px);
					}
				}
			}

			&.woocommerce-product-gallery--with-images {
				.woocommerce-product-gallery__image {
					&:not(:first-child) {
						@media only screen and (min-width: $laptop-landscape-plus-pixel) and (max-width: $laptop-landscape-large) {
							width: 90px;
						}

						@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape) {
							width: 82px;
						}

						@media only screen and (min-width: $ipad-portrait-plus-pixel) and (max-width: $ipad-landscape) {
							width: 140px;
						}
					}
				}
			}
		}

		.mkdf-new-product,
		.mkdf-onsale,
		.mkdf-out-of-stock{
			@media only screen and (min-width: $laptop-landscape-plus-pixel) and (max-width: $laptop-landscape-large) {
				left: 110px;
			}

			@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape) {
				left: 102px;
			}

			@media only screen and (min-width: $ipad-portrait-plus-pixel) and (max-width: $ipad-landscape) {
				left: 160px;
			}

			@include ipad-portrait{
				left: 10px;
			}
		}
	}
}

@include ipad-landscape {
    
    .mkdf-woo-single-page {
        
        .mkdf-single-product-content {
            
            .images,
            .mkdf-single-product-summary {
                float: none;
                width: 100%;
                padding: 0;
            }
	        
	        .images {
		
		        .woocommerce-product-gallery__trigger {
			        right: 10px;
		        }
	        }
            
            .mkdf-single-product-summary {
                margin: 30px 0 0;
            }
        }
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 13px 10px;
                    }
                }
            }
        }
    }
}

@include phone-landscape {

	.mkdf-woo-single-page {

		.mkdf-single-product-summary {
			.mkdf-quantity-buttons {
				margin: 0 6px 36px 0;
			}
		}

		.woocommerce-tabs {
			ul.tabs {
				li.additional_information_tab {
					border-right: 1px solid #d7f5fa;
				}
			}


		}

		&.mkdf-woo-single-thumb-on-left-side {

			.mkdf-single-product-content {

				.images {

					&.woocommerce-product-gallery--columns-4 {

						.woocommerce-product-gallery__image {

							&:not(:first-child) {
								width: 50%;
							}
						}
					}
				}
			}
		}

		.mkdf-single-product-content {

			.images {

				&.woocommerce-product-gallery--columns-4 {

					.woocommerce-product-gallery__image {

						&:not(:first-child) {
							width: 50%;
						}
					}
				}
			}
		}
	}
}
@include phone-portrait {

     .mkdf-woo-single-page {

        .woocommerce-tabs {

        	.entry-content {
        		padding: 55px 20px 59px;

        	}

            ul.tabs {
                li:first-child {
                    border-right: 1px solid #d7f5fa;
                }
            }
            
        }

     }
    .mkdf-woocommerce-page {

        .mkdf-content {
            table.group_table {
                .woocommerce-grouped-product-list-item__quantity {
                    display: block;

                }
                .woocommerce-grouped-product-list-item__label,
                .woocommerce-grouped-product-list-item__price {
                    display: inline-block;
                    width: auto;
                }

                
            }
            .variations {
                td.value {
                    margin-bottom: 8px;
                }

            }
            .single_variation_wrap {
                margin: 8px 0 0;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - end
   ========================================================================== */